const Provider = ({ children }) => {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [locations, setLocations] = useState([]);
  const [cartTotal, setCartTotal] = useState(0);
  useEffect(() => {
    setProducts(earlybird);
    setLocations(locationdata)
    calculateTotal();
  }, [cart]);

  const calculateTotal = () => {
    let total = 0;
    cart.forEach((item) => (total += item.count * item.price));
    setCartTotal(Number(total.toFixed(2)));
  };
  const manageCart = (action, product) => {
    let tempCart = [];
    let updatedProduct;
    let updatedProductIndex = 0;
    switch (action) {
      case "ADD_TO_CART":
        if (isProductInCart(product)) {
          return;
        }
        product.count = 1;

        setCart([...cart, product]);

        break;
      case "REMOVE_FROM_CART":
        setCart(cart.filter((cartItem) => cartItem.id !== product.id));

        break;
      case "EMPTY_CART":
        setCart([]);
        break;
      case "INCREASE_COUNT":
        tempCart = [...cart];
        updatedProductIndex = tempCart.findIndex(
          (item) => item.id === product.id
        );
        updatedProduct = tempCart[updatedProductIndex];
        updatedProduct.count++;
        tempCart[updatedProductIndex] = updatedProduct;
        setCart(tempCart);
        break;
      case "DECREASE_COUNT":
        tempCart = [...cart];
        updatedProductIndex = tempCart.findIndex(
          (item) => item.id === product.id
        );
        updatedProduct = tempCart[updatedProductIndex];
        if (updatedProduct.count === 1) {
          setCart(cart.filter((item) => item.id !== product.id));
          return;
        }
        updatedProduct.count--;
        tempCart[updatedProductIndex] = updatedProduct;
        setCart(tempCart);
        break;
      default:
        break;
    }
  };

  const isProductInCart = (item) => {
    if (cart.find((product) => product.id === item.id)) {
      return true;
    }
    return false;
  };

  const state = {
    isProductInCart,
    manageCart,
    cart,
    cartTotal,
    products,
    locations,
  };
  return <Context.Provider value={state}>{children}</Context.Provider>;
};

const useAppContext = () => useContext(Context);
const Consumer = Context;
export { Provider, useAppContext, Consumer };
