import React, { useState } from "react";
import { useAppContext } from "../Context/Context";
import { PaystackConsumer } from "react-paystack";
import { useToasts } from "react-toast-notifications";
function Form({ history }) {
  const context = useAppContext();
  const { products, cart, manageCart, cartTotal, locations } = context;
  const [selected, setSelected] = useState({});
  const [email, setEmail] = useState("");
  const [fullname, setFullname] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [Collection_mode, setCollection_mode] = useState("");
  const [Collection_point, setCollection_point] = useState("");
  const { addToast } = useToasts();
  const config = {
    reference: "ltc" + new Date().getTime(),
    email: email,
    amount: parseFloat(cartTotal * 100),
    // publicKey: "pk_live_97b36ef2946b928ab33b14aa4db2dadb8878c6aa",
    publicKey: "pk_test_0ad6d1b36602bd9d4d039f500d10dec6068b03d6",
    currency: "ghs",
    metadata: {
      fullname,
      email,
      phone,
      Collection_mode,
      Collection_point:Collection_point?JSON.parse(Collection_point):"",
      address,
      tickets:[...cart,]
      
    },
  };
  //   console.log(selected);
  const validate = (data) => {
    let complete = false;
    const {
      fullname,
      email,
      phone,
      Collection_mode,
      Collection_point,
      address,
    } = data;
    if (
      (fullname && email && phone && Collection_mode) ||
      (Collection_point && address)
    ) {
      complete = true;
    }
    return complete;
  };
  // console.log(locations)
  const handleSuccess = (reference) => {
    // Implementation for whatever you want to do with reference and after success call.
    // console.log(reference);
    // history.push("/dashboard");
    let  metadata= {
      fullname,
      email,
      phone,
      Collection_mode,
      Collection_point:JSON.parse(Collection_point),
      address,
      tickets:[...cart,]
      
    }
    console.log(metadata)
    addToast("Payment Successful", {
      appearance: "success",
      autoDismiss: true,
    });
  };

  const handleClose = () => {
    // implementation for  whatever you want to do when the Paystack dialog closed.
    // console.log("closed");
  };

  const componentProps = {
    ...config,
    text: "Paystack Button Implementation",
    onSuccess: (reference) => handleSuccess(reference),
    onClose: handleClose,
  };

  return (
    <section class="banner-bottom contact" id="contact">
      <div class="container-fluid">
        <div class="inner-sec-w3ls-agileits p-lg-5 p-3 row">
          <div class="col-md-8 col-xs-12">
            <div class="card">
              <div class="card-header">
                <h4 class="card-title">Please Complete The Form</h4>
              </div>
              <div class="card-content mt-2">
                <div class="card-body">
                  <form action="#" class="wizard-horizontal">
                    <h6>
                      <i class="step-icon"></i>
                      <span class="fonticon-wrap">
                        <i
                          class=" -evo"
                          data-options="name:morph-doc.svg; size: 50px; style:lines; strokeColor:#adb5bd;"
                        ></i>
                      </span>
                    </h6>

                    <fieldset>
                      <div class="row">
                        <div class="col-12">
                          <h6 class="py-50">Enter Your Personal Details</h6>
                        </div>
                        <hr />
                        <div class="col-sm-6">
                          <div class="form-group">
                            <label for="firstName13">Full Name </label>
                            <input
                              type="text"
                              class="form-control"
                              id="firstName13"
                              placeholder="Enter Your Full Name"
                              onChange={(e) => setFullname(e.target.value)}
                            />
                          </div>
                        </div>
                        <div class="col-sm-6">
                          <div class="form-group">
                            <label for="emailAddress1">Email</label>
                            <input
                              type="email"
                              class="form-control"
                              placeholder="Enter Your Email"
                              onChange={(e) => setEmail(e.target.value)}
                            />
                          </div>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-sm-6">
                          <div class="form-group">
                            <label>Phone</label>
                            <input
                              type="text"
                              class="form-control"
                              placeholder="Enter Your Phone Number"
                              maxLength={10}
                              minLength={10}
                              onChange={(e) => setPhone(e.target.value)}
                            />
                          </div>
                        </div>

                        <div class="col-sm-6">
                          <div class="form-group">
                            <label>Address</label>
                            <input
                              type="text"
                              class="form-control"
                              placeholder="House number/street name"
                              onChange={(e) => setAddress(e.target.value)}
                            />
                          </div>
                        </div>
                      </div>
                    </fieldset>

                    <h6>
                      <i class="step-icon"></i>
                      <span class="fonticon-wrap">
                        <i
                          class=" -evo"
                          data-options="name:truck.svg; size: 50px; style:lines; strokeColor:#adb5bd;"
                        ></i>
                      </span>
                    </h6>

                    <fieldset>
                      <div class="row">
                        <div class="col-12">
                          <h6 class="py-50">Tickets</h6>
                        </div>
                        <hr />

                        <div class="col-sm-6">
                          <label>Type Of Ticket</label>
                          <div className="row">
                            <div className="col-8">
                              <div class="form-group">
                                <select
                                  name="country"
                                  onChange={(e) => setSelected(e.target.value)}
                                  required
                                  class="form-control"
                                >
                                  <option value="">---select ticket---</option>
                                  {products.map((item) => (
                                    <option value={JSON.stringify(item)}>
                                      {item.name}
                                    </option>
                                  ))}
                                </select>
                              </div>
                            </div>

                            <div className="col-4">
                              <div class="form-group">
                                <button
                                  type="button"
                                  className="btn btn-primary btn-sm form-control"
                                  onClick={(e) => {
                                    e.preventDefault();
                                    manageCart(
                                      "ADD_TO_CART",
                                      JSON.parse(selected)
                                    );
                                    addToast("Item added to cart", {
                                      appearance: "success",
                                      autoDismiss: true,
                                    });
                                  }}
                                >
                                  add
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="row">
                        <div class="col-sm-6">
                          <div class="form-group">
                            <label>Collection Time</label>
                            <p>
                              Collection time is 8am to 5pm. Monday to friday
                            </p>
                          </div>
                        </div>
                      </div>
                    </fieldset>
                    <fieldset>
                      <div class="row">
                        <div class="col-12">
                          <h6 class="py-50">Collection Details</h6>
                        </div>
                        <hr />
                      </div>
                      <div class="row">
                        <div class="col-sm-6">
                          <div class="form-group">
                            <label>Collection Mode</label>
                            <select
                              name="country"
                              class="form-control"
                              onChange={(e) =>
                                setCollection_mode(e.target.value)
                              }
                            >
                              <option value="">Select</option>
                              <option value="Pick-up">Pick-Up</option>
                              <option value="Delivery">Delivery</option>
                            </select>
                          </div>
                        </div>
                        {Collection_mode === "Pick-up" ? (
                          <div class="col-sm-6">
                            <div class="form-group">
                              <label>Collection Point</label>
                              <select
                                name="country"
                                class="form-control"
                                onChange={(e) =>
                                  setCollection_point(e.target.value)
                                }
                              >
                                <option value="">
                                  ---Select a pickup location---
                                </option>
                                {locations.map((item) => (
                                  <option value={JSON.stringify(item)}>
                                     {item.provider} 🎈 {item.location} 🎈 {item.contact}
                                  </option>
                                ))}
                              </select>
                              <div className="">
                                {/* {JSON.parse(Collection_point).location} */}
                              </div>
                            </div>
                          </div>
                        ) : null}
                      </div>
                    </fieldset>

                    <h6>
                      <i class="step-icon"></i>
                      <span class="fonticon-wrap">
                        <i
                          class=" -evo"
                          data-options="name:home.svg; size: 50px; style:lines; strokeColor:#adb5bd;"
                        ></i>
                      </span>
                    </h6>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-4 col-xs-12">
            <div class="card">
              <div class="card-header">
                <h4 class="card-title">Cart</h4>
              </div>
              <div class="card-content mt-2">
                <div class="card-body">
                  <table class="table table-bordered table-responsive">
                    <thead>
                      <tr>
                        <th>Title</th>
                        <th>Quantity</th>
                        <th>Price</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {cart && cart.length > 0 ? (
                        <>
                          {cart.map((item) => (
                            <tr>
                              <td>{item.name}</td>
                              <td>
                                <div
                                  className="row"
                                  style={{ justifyContent: "center" }}
                                >
                                  <button
                                    disabled={item.count <= 1 ? true : false}
                                    style={{
                                      background: "#fafafa",
                                      borderWidth: "0px",
                                      padding: "10px",
                                      borderRadius: "60px",
                                      width: "30px",
                                      height: "30px",

                                      justifyContent: "center",
                                      alignItems: "center",
                                      display: "flex",
                                      fontSize: "15px",
                                    }}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      manageCart("DECREASE_COUNT", item);
                                    }}
                                  >
                                    -
                                  </button>{" "}
                                  <p
                                    style={{
                                      margin: "3px",
                                    }}
                                  >
                                    {" "}
                                    {item.count}
                                  </p>
                                  <button
                                    style={{
                                      background: "#fafafa",
                                      borderWidth: "0px",
                                      padding: "10px",
                                      borderRadius: "60px",
                                      width: "30px",
                                      height: "30px",

                                      justifyContent: "center",
                                      alignItems: "center",
                                      display: "flex",

                                      fontSize: "15px",
                                    }}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      manageCart("INCREASE_COUNT", item);
                                    }}
                                  >
                                    +
                                  </button>
                                </div>
                              </td>

                              <td>GH&#8373;{item.price}</td>
                              <td>
                                <button
                                  className=""
                                  style={{
                                    background: "#00000000",
                                    borderWidth: "0px",
                                  }}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    manageCart("REMOVE_FROM_CART", item);
                                  }}
                                >
                                  <i className="fa fa-trash text-danger"></i>
                                </button>
                              </td>
                            </tr>
                          ))}
                          <tr className="" style={{ background: "#fafafa" }}>
                            <td colSpan={3}>Total</td>
                            <td>GH&#8373;{cartTotal.toFixed(2)}</td>
                          </tr>

                          <tr>
                            <td colSpan={4}>
                              <PaystackConsumer {...componentProps}>
                                {({ initializePayment }) => (
                                  <button
                                    className="btn btn-primary w-100"
                                    onClick={() => {
                                      let data = {
                                        fullname,
                                        email,
                                        phone,
                                        Collection_mode,
                                        Collection_point,
                                        address,
                                      };
                                      if (validate(data)) {
                                        initializePayment(
                                          handleSuccess,
                                          handleClose
                                        );
                                      } else {
                                        alert(
                                          "Please make sure all fields are not left empty"
                                        );
                                      }
                                    }}
                                  >
                                    Pay now
                                  </button>
                                )}
                              </PaystackConsumer>
                            </td>
                          </tr>
                        </>
                      ) : (
                        <tr>
                          <td colSpan={4} className="text-center">
                            No item in cart
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Form;
